.textual {
  font-size: rem(20);
  line-height: 1.5;
  font-weight: 400;
  color: lighten($base-color, 25%);

  & > * + *:not(li) {
    margin-top: 2rem;
    margin-bottom: 0;
  }

  & > div {
    max-width: 100%;
  }

  & > h1,
  & > h2,
  & > h3,
  & > h4,
  & > h5,
  & > h6 {
    font-weight: 500;
    font-family: $primary-font;
  }

  & > h2 {
    font-size: rem(28);
  }

  & > h3 {
    font-size: rem(25);
  }

  & > * + h2 {
    margin-top: 3rem;
  }

  & > * + h3 {
    margin-top: 2.5rem;
  }

  & > h2 + * {
    margin-top: 1rem;
  }

  & > h3 + * {
    margin-top: 0.9rem;
  }

  & > h4 + * {
    margin-top: 0.8rem;
  }

  & > h2 + h3 {
    // margin-top: 0.8rem;
  }

  & > img,
  p > img,
  div > img {
    display: block;
    margin-left: auto;
    margin-right: auto;
  }

  @include mq-min(1024px) {
    font-size: vh(19);

    .alignleft {
      float: left;
      margin-right: $gutter;
    }

    .alignright {
      float: right;
      margin-left: $gutter;
    }
  }
}
